body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}


.App-header {
  background-color: lightgrey;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


.API__main {
    min-width:60vw;
    margin-top:25px;
    border-radius: 7px;
    background-color:white;
    display:flex;
    flex-direction: column;
}

.API__search {
    border:none;
    padding:5px;
    margin: 10px;
    border-bottom:1px solid lightgrey;
    font-size:0.8em;
    color:grey;
}

input:focus {
    outline:none;
}
.studentCard {
    color:black;
    display:flex;
    flex-direction:row;
}

.StudentCard__col {
    display:flex;
    flex-direction:column;
    min-width:100%;
}

.studentPhoto {
    border-radius:50%;
    border:1px solid lightgrey;
    height:100px;
    margin-right:25px;
}

h1 {
    text-transform:uppercase;
    margin:0;
    font-size:1em;
    padding-bottom:3px;
}

p {
    font-size:12px;
    margin:0;
    text-align:left;
    padding-left:15px;
    color:darkgrey;
}

.StudentCard__main {
    min-width:60vw;
    margin-top:25px;
    min-height:100vh;
    border-radius: 7px;
    background-color:white;
    display:flex;
    flex-direction: column;
}

.panel {
    /* position:relative;
    top:-25px;
    left:15%;
    z-index:5; */

}

ul {
    list-style:none;
    padding:0;
}

li {
    color:darkgrey;
    font-size:12px;
    text-align:left;
    padding-left:15px;
}

.StudentCard__row{
    display:flex;
    flex-direction:row;
    justify-content:space-between;
}

.plusSign {
    /* display:flex; */
    align-self:flex-end;
    max-height:1em;
    color:darkgrey;
}

.fa-plus {
    color:darkgrey;
    position:absolute;
    right:15px;
    top:15px;
}
